import fs from "fs";
import path from "path";
import matter from "gray-matter";

export interface ReviewMetadata {
  slug: string;
  title: string;
  verdict: "calm-down" | "caution" | "red-flag" | "overhyped"; // you can add more later
  tags: string[];
  field: string;
  date: string;   // ISO
  summary: string;
  paperLink?: string;
}

export interface ReviewFile extends ReviewMetadata {}

const SERIOUS_DIR = path.join(process.cwd(), "content", "serious");

export function getAllReviews(): ReviewMetadata[] {
  const files = fs.readdirSync(SERIOUS_DIR).filter(f => f.endsWith(".mdx"));
  const items = files.map((filename) => {
    const filePath = path.join(SERIOUS_DIR, filename);
    const file = fs.readFileSync(filePath, "utf8");
    const { data } = matter(file);
    return data as ReviewMetadata;
  });

  // newest first
  return items.sort((a, b) => (a.date > b.date ? -1 : 1));
}

export function getReviewBySlug(slug: string) {
  const filePath = path.join(SERIOUS_DIR, `${slug}.mdx`);
  const source = fs.readFileSync(filePath, "utf8");
  const { content, data } = matter(source);
  return { content, meta: data as ReviewMetadata };
}

export function getAllReviewSlugs() {
  return fs
    .readdirSync(SERIOUS_DIR)
    .filter(f => f.endsWith(".mdx"))
    .map(f => f.replace(/\.mdx$/, ""));
}
